<template>
  <div class="selection">
    <div id="items">
      <div class="question-div">
        <p class="green">{{ question }}</p>
      </div>
      <div class="item" @click="pressed(true)">
        <p>是</p>
      </div>
      <div class="item" @click="pressed(false)">
        <p>否</p>
      </div>
    </div>
    <div id="wrong-div" v-if="showHintDescription">
      <p v-bind:class="[isUserAnswerCorrect? 'green' : 'red']">{{ hintDescription }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SelectionYesNo',
  data () {
    return {
      showHintDescription: false,
      time: null,
      isUserAnswerCorrect: false,
      hintDescription: ''
    }
  },
  props: {
    question: String,
    correctOption: Boolean,
    wrongDescription: String,
    isItCorrectAnswer: Function
  },
  methods: {
    pressed (yesOrNo) {
      if (yesOrNo === true && this.correctOption === true) {
        this.hintDescription = '答對了!'
        this.isUserAnswerCorrect = true
      }

      if (yesOrNo === true && this.correctOption === false) {
        this.hintDescription = '答錯了!是' + this.wrongDescription
        this.isUserAnswerCorrect = false
      }

      if (yesOrNo === false && this.correctOption === true) {
        this.hintDescription = '答錯了!這段描述沒錯'
        this.isUserAnswerCorrect = false
      }

      if (yesOrNo === false && this.correctOption === false) {
        this.hintDescription = '答對了!是' + this.wrongDescription
        this.isUserAnswerCorrect = true
      }

      this.showHintDescription = true

      this.time = setInterval(() => {
        this.isItCorrectAnswer(this.isUserAnswerCorrect)
        this.showHintDescription = false
        clearTimeout(this.time)
      }, 2000)
    }
  }
}
</script>

<style scoped>
  .selection {
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    z-index: 10;
    min-height: 100vh;
    width: 100%;
  }
  .item {
    background-image: url(../assets/images/commute/select-button-bg.png);
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px 0px;
    width: 100%;
    height: 80px;
  }
  .question-div {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px 0px;
    width: 100%;
    height: 80px;
  }
  p {
    color: white;
    margin: 0px 20px;
  }
  .green {
    color: #95ff00;
  }
  #wrong-div {
    position: absolute;
    width: 100%;
    height: 70%;
    /* background-color: orange; */
  }
  .red {
    color: #ff0000;
  }
</style>



