<template>
  <div class="main-div">
    <div class="main-bg">
      <DialogueTheme class="theme" :msgs="message_14" :speaker="speaker" :finishConversation="finishConversation_14" v-if="showDialogue_14" />
      <Selection id="selection" :items="selectionItems_14" v-if="showSelection_14" :finishSelection="finishSelection_14" />
      <DialogueTheme class="theme" :msgs="message_14_1" :speaker="speaker" :finishConversation="finishConversation_14_1" v-if="showDialogue_14_1" />
      <DialogueTheme class="theme" :msgs="message_14_2" :speaker="speaker" :finishConversation="finishConversation_14_2" v-if="showDialogue_14_2" />

      <SelectionYesNo :question="question_14_3_1" v-if="showQuestion_14_3_1" :isItCorrectAnswer="isItCorrect_14_3_1" :correctOption="true" :wrongDescription="''" />
      <SelectionYesNo :question="question_14_3_2" v-if="showQuestion_14_3_2" :isItCorrectAnswer="isItCorrect_14_3_2" :correctOption="false" :wrongDescription="'達摩神像'" />
      <SelectionYesNo :question="question_14_3_3" v-if="showQuestion_14_3_3" :isItCorrectAnswer="isItCorrect_14_3_3" :correctOption="true" :wrongDescription="''" />
      <SelectionYesNo :question="question_14_3_4" v-if="showQuestion_14_3_4" :isItCorrectAnswer="isItCorrect_14_3_4" :correctOption="false" :wrongDescription="'麥香紅茶'" />
      <SelectionYesNo :question="question_14_3_5" v-if="showQuestion_14_3_5" :isItCorrectAnswer="isItCorrect_14_3_5" :correctOption="false" :wrongDescription="'千禧曼波'" />

      <DialogueTheme class="theme" :msgs="message_end_a" :speaker="speaker" :finishConversation="finishConversation_end_a" v-if="showDialogue_end_a" />
      <DialogueTheme class="theme" :msgs="message_end_b" :speaker="speaker" :finishConversation="finishConversation_end_b" v-if="showDialogue_end_b" />

      <div id='avator'></div>
    </div>
  </div>
</template>

<script>
import DialogueTheme from '@/components/DialogueTheme'
import Selection from '@/components/Selection'
import SelectionYesNo from '@/components/SelectionYesNo'

export default {
  name: 'CommuteSouthMainStation',
  components: {
    DialogueTheme,
    Selection,
    SelectionYesNo
  },
  data () {
    return {
      speaker: '阿  勤',
      showDialogue_14: true,
      message_14: ['路上看到的所有一切，都很美好呢。對了，你有注意到一些有趣的小事嗎？'],
      showSelection_14: false,
      selectionItems_14: ['抱歉，我還有任務要完成呢。',
        '啊，抱歉⋯⋯我什麼也沒注意到。',
        '有啊、有啊，我說說看⋯⋯'],
      showDialogue_14_1: false,
      message_14_1: ['啊，說的也是⋯⋯謝謝你陪我啦，我很滿足。那麼⋯⋯再見啦，要記得享受生活，別太努力工作呀。（笑）'],
      showDialogue_14_2: false,
      message_14_2: ['是嗎⋯⋯那真是太可惜了。總之，謝謝你陪我啦，我很滿足。那麼⋯⋯再見啦，要記得享受生活，別太努力工作呀。（笑）'],
      showQuestion_14_3_1: false,
      question_14_3_1: '從基隆出發後經過一段黑漆的隧道，只有燈管和逃生號誌在黑暗中發出一絲光芒。',
      showQuestion_14_3_2: false,
      question_14_3_2: '通往台北的高架橋出現在左側，那裡有一座廟「無極理天賢武天宮」，他頂樓有座金色的觀音神像。',
      showQuestion_14_3_3: false,
      question_14_3_3: '經過平交道，左側圍牆裡那些看不見的一間間昏紅的小房間，是著名的「鐵路街」豆干厝。',
      showQuestion_14_3_4: false,
      question_14_3_4: '高中生在車廂裡打翻的飲料，是熟悉的麥香最對味，麥香綠茶。',
      showQuestion_14_3_5: false,
      question_14_3_5: '伯伯收音機播放的音樂，是侯孝賢拍的《寶島曼波》舒淇在基隆陸橋片段，林強的歌聲。',
      showDialogue_end_a: false,
      message_end_a: ['哇，真是太驚人了。你竟然可以在平凡的交通過程，仍然保持對當下的敏銳覺知，你果然⋯⋯是個了不起的人呢。如果是被你這樣的館員帶回去，我想我沒什麼遺憾了，謝謝你。', '對了，有空的話到螞蟻豆花店吧！只要跟店員說：「搭火車蠻有趣的」就可以拿到我的小禮物囉。'],
      showDialogue_end_b: false,
      message_end_b: ['哈哈哈，你很努力回想呢，但是看來在移動的過程中，你也不小心錯失了許多呢。記得保持對生活的覺知啊，這樣我們才不會變成「現實」這台車的零件唷。']
    }
  },
  mounted () {
    this.$store.commit('changeHeader', false)
    this.$store.commit('showFooter', true)
  },
  methods: {
    finishConversation_14 (msgs) {
      this.showDialogue_14 = false
      this.showSelection_14 = true
    },
    finishSelection_14 (index) {
      this.showSelection_14 = false
      console.log(index)
      switch (index) {
        case 0:
          this.showDialogue_14_1 = true
          break
        case 1:
          this.showDialogue_14_2 = true
          break
        case 2:
          this.showQuestion_14_3_1 = true
          break
        default:
          break
      }
    },
    finishConversation_14_1 (msgs) {
      this.showDialogue_14_1 = false
      this.$router.push({
        path: './commute-kim-end'
      })
    },
    finishConversation_14_2 (msgs) {
      this.showDialogue_14_2 = false
      this.$router.push({
        path: './commute-kim-end'
      })
    },
    isItCorrect_14_3_1 (correct) {
      if (correct) {
        this.$store.commit('addKimFinalQuestionsAnswerCorrectNumber')
        console.log(this.$store.state.kimFinalQuestionsAnswerCorrectNumber)
      }
      this.showQuestion_14_3_1 = false
      this.showQuestion_14_3_2 = true
    },
    isItCorrect_14_3_2 (correct) {
      if (correct) {
        this.$store.commit('addKimFinalQuestionsAnswerCorrectNumber')
        console.log(this.$store.state.kimFinalQuestionsAnswerCorrectNumber)
      }
      this.showQuestion_14_3_2 = false
      this.showQuestion_14_3_3 = true
    },
    isItCorrect_14_3_3 (correct) {
      if (correct) {
        this.$store.commit('addKimFinalQuestionsAnswerCorrectNumber')
        console.log(this.$store.state.kimFinalQuestionsAnswerCorrectNumber)
      }
      this.showQuestion_14_3_3 = false
      this.showQuestion_14_3_4 = true
    },
    isItCorrect_14_3_4 (correct) {
      if (correct) {
        this.$store.commit('addKimFinalQuestionsAnswerCorrectNumber')
        console.log(this.$store.state.kimFinalQuestionsAnswerCorrectNumber)
      }
      this.showQuestion_14_3_4 = false
      this.showQuestion_14_3_5 = true
    },
    isItCorrect_14_3_5 (correct) {
      if (correct) {
        this.$store.commit('addKimFinalQuestionsAnswerCorrectNumber')
        console.log(this.$store.state.kimFinalQuestionsAnswerCorrectNumber)
      }
      this.showQuestion_14_3_5 = false
      if (this.$store.state.kimFinalQuestionsAnswerCorrectNumber >= 3) {
        this.showDialogue_end_a = true
      } else {
        this.showDialogue_end_b = true
      }
    },

    finishConversation_end_a (msgs) {
      this.showDialogue_end_a = false
      this.$router.push({
        path: './commute-kim-end'
      })
    },
    finishConversation_end_b (msgs) {
      this.showDialogue_end_b = false
      this.$router.push({
        path: './commute-kim-end'
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "~@/assets/scss/common.scss";
  .main-div {
    @extend fullScreenImage;
    background-image: url('~@/assets/images/commute/main-bg.png');
    .main-bg {
      @extend fullScreenImage;
      background-image: url('~@/assets/images/commute/sanken-bg.png');
      background-size: contain;
      position: relative;
      #avator {
        @extend fullScreenImage;
        background-image: url('~@/assets/images/commute/passenger-colorful-head.png');
        z-index: 0;
      }
      .theme {
        position: absolute;
        bottom: 15%;
      }
    }
  }
</style>



